.header {
    padding: 32px 0;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--space-between);
}

.header__logo {
    font-size: 32px;
    font-weight: bold;
    letter-spacing: -1px;
    line-height: 90%;
}

.header__link:not(:last-child) {
    margin-right: 24px;
}

.header__link:hover {
    text-decoration: underline;
}

.header__link:focus {
    text-decoration: underline;
}