.footer {
    padding: 32px 0;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--space-between);
}

.footer__link:not(:last-child) {
    margin-right: 40px;
}

.footer__link:hover {
    text-decoration: underline;
}

.footer__link:focus {
    text-decoration: underline;
}

.footer__copyright {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -1px;

    color: var(--clr-grey);
}