:root {
    --flex: flex;
    --grid: grid;
    --column: column;
    --center: center;
    --none: none;
    --space-between: space-between;

    --clr-white: #fff;
    --clr-black: #000;
    --clr-green: #00aa45;
    --clr-grey: #8e8e8e;
}

/*basic*/

html {
    scroll-behavior: smooth;
}

body {
    font-family: "Inter";
    background-color: var(--clr-white);
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background-color: var(--clr-grey);
}

::-webkit-scrollbar-track {
    background-color: var(--clr-white);
}

h1 {
    font-size: 65px;
    line-height: auto;
    font-weight: normal;
}

h2 {
    font-size: 48px;
    line-height: auto;
    font-weight: bold;
    letter-spacing: -1%;
}

h3 {
    font-size: 20px;
    line-height: 140%;
    font-weight: bold;
    letter-spacing: -1%;
}

a {
    font-size: 16px;
    line-height: auto;
    font-weight: normal;
    
    color: var(--clr-black);
    text-decoration: var(--none);
}

p {
    color: var(--clr-grey);

    font-size: 20px;
    line-height: 140%;
    font-weight: normal;
    letter-spacing: -1%;
}

button {
    border: var(--none);
    outline: var(--none);
    cursor: pointer;
}

img {
    display: block;
}

.container {
    margin: 0 auto;
    max-width: 80%;
}

.section {
    padding-top: 120px;

    display: var(--flex);
    flex-direction: var(--column);
    gap: 42px;
}

.section__header {
    max-width: 790px;

    display: var(--flex);
    flex-direction: var(--column);
    gap: 10px;
}

.section__title {
    margin: 0;
}

.section__text {
    margin: 0;
}

/*basic*/

/*hero*/

.hero {
    padding: 120px 0 0;

    display: var(--flex);
    align-items: flex-start;
}

/*hero*/

/*case*/

.case__list {
    width: 100%;

    display: var(--grid);
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    row-gap: 32px;
}

.case__item-wallpaper {
    position: relative;

    display: var(--flex);
    justify-content: var(--center);
    align-items: var(--center);
}

.case__cursor {
    position: absolute;

    height: 128px;
    width: 128px;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--center);

    border-radius: 64px;
    background-color: var(--clr-green);

    transform: scale(0);
    transition: 0.3s ease-in-out;
}

.case__item-wallpaper:hover .case__cursor {
    transform: scale(1);
}

.case__item-wrap {
    display: var(--flex);
    flex-direction: var(--column);
    gap: 24px;
}

.case-item__picure {
    width: 100%;
}

.case__item-picture {
    width: 100%;
    object-fit: cover;
}

.case__item-article {
    display: var(--flex);
    flex-direction: var(--column);
    gap: 6px;
}

.case__item-title {
    margin: 0;
}

.case__item-text {
    margin: 0;
}

/*case*/

/*services*/

.services__list {
    width: 100%;
}

.services__item {
    padding: 20px 0;
    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--space-between);

    border-bottom: 1px solid var(--clr-grey);
}

.services__title {
    font-size: 26px;
    font-weight: 400;
    letter-spacing: -1px;
    line-height: auto;
}

.services__article {
    display: var(--flex);
    flex-direction: var(--column);
    gap: 10px;
}

.services__text {
    margin: 0;

    font-size: 14px;
    letter-spacing: -0.4px;
    line-height: auto;

    color: var(--clr-black);
}

.services__subtitle {
    margin: 0;

    font-size: 14px;
    letter-spacing: -0.4px;
    line-height: auto;
}

.services__link {
    padding: 27px 28px;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--center);

    border: 1px solid var(--clr-grey);
    border-radius: 64px;
    transition: 0.3s ease-in-out;
}

.services__link:hover {
    background-color: var(--clr-grey);
}

.services__arrow {
    width: 16px;
    height: 18px;

    transition: 0.3s ease-in-out;
}

.services__link:hover .services__arrow path {
    fill: var(--clr-white);
    stroke: var(--clr-white);
}

.services__arrow path {
    fill: var(--clr-grey);
    stroke: var(--clr-grey);

    transition: 0.3s ease-in-out;
}

/*services*/

/*blog*/

.blog__list {
    display: var(--grid);
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
}

.blog__item-wallpaper {
    position: relative;

    display: var(--flex);
    justify-content: var(--center);
    align-items: var(--center);

    overflow: hidden;
    border-radius: 32px;
}

.blog__cursor {
    position: absolute;

    height: 128px;
    width: 128px;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--center);

    border-radius: 64px;
    background-color: var(--clr-green);
    transform: scale(0);
    transition: 0.3s ease-in-out;
}

.blog__item-wallpaper:hover .blog__cursor {
    transform: scale(.6);
}

.blog__item-picture {
    height: 380px;
    width: 100%;
    object-fit: cover;
}

/*blog*/

/*contact*/

.contact {
    padding-bottom: 120px;

    display: var(--flex);
    align-items: var(--center);
    justify-content: var(--center);
    gap: 48px;
}

.contact__title {
    margin: 0;

    font-size: 140px;
    letter-spacing: -2px;
}

.contact__btn {
    margin: 0;
    padding: 32px;

    width: 204px;

    text-align: var(--center);

    border-radius: 64px;
    color: var(--clr-white);
    background-color: var(--clr-black);

    transition: 0.3s ease-in-out;
}

.contact__btn:hover {
    outline: 1px solid var(--clr-black);
    color: var(--clr-black);
    background-color: var(--clr-white);
}

/*contact*/

@media screen and (max-width: 1024px) {
    
    .container {
        max-width: 90%;
    }

}