.open-more {
    width: 100%;
    display: var(--flex);
    justify-content: var(--center);
}

.open-more__btn {
    display: var(--flex);
    align-items: var(--center);
    gap: 8px;

    background-color: transparent;
}

.open-more__arrow {
    width: 13px;
    height: 15px;

    transition: 0.3s ease-in-out;
}

.open-more__arrow path {
    fill: var(--clr-black);
    stroke: var(--clr-black);
}

.open-more__btn:hover .open-more__arrow {
    margin-left: 8px;
}